.button {
    padding: 10px 35px;
    border-radius: 3px;
    border: none;
    font-size: 16px;
    transition: 0.5s ease;
    color: #fff;
    cursor: pointer;
}

.disabled {
    background: #004D99;
    opacity: 0.5;
    color: #002E5C;
}

.secondary {
    border: 1px solid #06C;
    color: #06C;
}

.default {
    background: #06C;
}

.red {
    background: #CC1439;
}

.default:hover {
    background: #0080FF;
}

.red:hover {
    background: #FF1A47;
}

.secondary:hover {
    background: #E5F0FA;
}


.default:active {
    background: #0059B2;
}

.secondary:active {
    background: #C9E5FF;
}

.red:active {
    background: #B21232;
}

.button:disabled {
}
